/* ========================================
   RYNEDITOR - RESPONSIVE DESIGN SYSTEM
   ======================================== */

/* Variables CSS para breakpoints dinámicos */
:root {
  --breakpoint-mobile: 480px;
  --breakpoint-tablet: 768px;
  --breakpoint-desktop: 1024px;
  --breakpoint-wide: 1200px;
  
  /* Variables de layout responsivo */
  --sidebar-width-mobile: 100%;
  --sidebar-width-desktop: 60px;
  --sidebar-height-mobile: auto;
  --sidebar-height-desktop: 100vh;
  
  /* Variables de espaciado responsivo */
  --padding-mobile: 8px;
  --padding-tablet: 12px;
  --padding-desktop: 16px;
  
  /* Variables de tipografía responsiva */
  --font-size-mobile: 12px;
  --font-size-tablet: 14px;
  --font-size-desktop: 16px;
}

/* ========================================
   LAYOUT PRINCIPAL RESPONSIVO
   ======================================== */

/* Móviles (hasta 768px) */
@media (max-width: 768px) {
  /* Contenedor principal - reorganizar en columna */
  .AppContainer,
  div[style*="display: flex"][style*="flex-direction: column"][style*="height: 100vh"] {
    flex-direction: column !important;
    height: 100vh !important;
  }
  
  /* Contenido principal - eliminar margen del sidebar */
  .MainContent,
  div[style*="display: flex"][style*="flex: 1"][style*="margin-left: 60px"] {
    margin-left: 0 !important;
    flex-direction: column !important;
    overflow: visible !important;
  }
  
  /* Contenedor del editor - reorganizar paneles */
  .EditorContainer,
  div[style*="display: flex"][style*="flex: 1"][style*="background-color: var(--color-backgroundSecondary)"] {
    flex-direction: column !important;
    height: auto !important;
    min-height: calc(100vh - 60px);
  }
  
  /* Sidebar - hacer responsive */
  .SidebarContainer,
  div[style*="position: fixed"][style*="left: 0"][style*="width: 60px"] {
    position: relative !important;
    width: 100% !important;
    height: auto !important;
    left: auto !important;
    top: auto !important;
    z-index: auto !important;
  }
  
  /* TopBar - ajustar para móviles */
  .TopBar,
  div[style*="display: flex"][style*="justify-content: space-between"][style*="padding: 16px"] {
    padding: 8px 12px !important;
    flex-wrap: wrap !important;
  }
}

/* Tablets (769px - 1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
  /* Layout híbrido para tablets */
  .MainContent,
  div[style*="display: flex"][style*="flex: 1"][style*="margin-left: 60px"] {
    margin-left: 0 !important;
    flex-direction: column !important;
  }
  
  .SidebarContainer,
  div[style*="position: fixed"][style*="left: 0"][style*="width: 60px"] {
    position: relative !important;
    width: 100% !important;
    height: auto !important;
  }
  
  .EditorContainer,
  div[style*="display: flex"][style*="flex: 1"][style*="background-color: var(--color-backgroundSecondary)"] {
    flex-direction: row !important;
    height: calc(100vh - 80px) !important;
  }
}

/* ========================================
   COMPONENTES ESPECÍFICOS RESPONSIVOS
   ======================================== */

/* Paneles y contenedores */
@media (max-width: 768px) {
  /* Reducir padding en todos los paneles */
  .PanelContainer,
  .Section,
  div[style*="padding: 16px"],
  div[style*="gap: 16px"] {
    padding: var(--padding-mobile) !important;
    gap: var(--padding-mobile) !important;
  }
  
  /* Ajustar tamaños de fuente */
  .SectionTitle,
  h1, h2, h3, h4, h5, h6 {
    font-size: var(--font-size-mobile) !important;
  }
  
  /* Botones más grandes para touch */
  button,
  .Button,
  div[role="button"] {
    min-height: 44px !important;
    min-width: 44px !important;
    padding: 8px 12px !important;
  }
  
  /* Inputs más grandes para móviles */
  input,
  textarea,
  select {
    min-height: 44px !important;
    font-size: 16px !important; /* Previene zoom en iOS */
  }
}

/* ========================================
   NAVEGACIÓN RESPONSIVA
   ======================================== */

@media (max-width: 768px) {
  /* Menú de navegación - reorganizar */
  .TabsContainer,
  div[style*="display: flex"][style*="flex-direction: column"][style*="padding: 16px 0"] {
    flex-direction: row !important;
    flex-wrap: wrap !important;
    padding: 8px !important;
    justify-content: center !important;
  }
  
  /* Botones de navegación más grandes */
  .TabButton,
  div[role="button"][style*="padding: 8px"],
  button[style*="padding: 8px"] {
    min-width: 60px !important;
    min-height: 60px !important;
    margin: 4px !important;
  }
}

/* ========================================
   EDITOR DE SCRIPT RESPONSIVO
   ======================================== */

@media (max-width: 768px) {
  /* Editor de código - pantalla completa */
  .ScriptEditor,
  .CodeEditor,
  div[style*="flex: 1"][style*="overflow: hidden"] {
    height: 50vh !important;
    min-height: 300px !important;
  }
  
  /* Panel de vista previa - debajo del editor */
  .PreviewPanel,
  div[style*="width: 400px"][style*="border-left"] {
    width: 100% !important;
    height: 50vh !important;
    border-left: none !important;
    border-top: 1px solid var(--color-border) !important;
  }
}

/* ========================================
   MODALES Y OVERLAYS RESPONSIVOS
   ======================================== */

@media (max-width: 768px) {
  /* Modales - pantalla completa en móviles */
  .Modal,
  .ModalOverlay,
  div[style*="position: fixed"][style*="z-index: 1000"] {
    padding: var(--padding-mobile) !important;
  }
  
  .ModalContent,
  div[style*="max-width: 600px"],
  div[style*="max-width: 800px"] {
    max-width: 100% !important;
    width: 100% !important;
    height: 100% !important;
    max-height: 100% !important;
    margin: 0 !important;
    border-radius: 0 !important;
  }
}

/* ========================================
   UTILIDADES RESPONSIVAS
   ======================================== */

/* Clases de utilidad para componentes que las necesiten */
.mobile-hidden {
  display: none !important;
}

@media (min-width: 769px) {
  .mobile-hidden {
    display: block !important;
  }
  
  .desktop-hidden {
    display: none !important;
  }
}

.mobile-full-width {
  width: 100% !important;
}

.mobile-stack {
  flex-direction: column !important;
}

/* ========================================
   OPTIMIZACIONES ESPECÍFICAS PARA MÓVILES
   ======================================== */

@media (max-width: 768px) {
  /* Mejorar rendimiento en móviles */
  * {
    -webkit-overflow-scrolling: touch;
  }
  
  /* Prevenir zoom en inputs */
  input[type="text"],
  input[type="email"],
  input[type="password"],
  input[type="number"],
  textarea,
  select {
    font-size: 16px !important;
  }
  
  /* Optimizar scroll */
  .overflow-y-auto,
  div[style*="overflow-y: auto"] {
    -webkit-overflow-scrolling: touch;
    scroll-behavior: smooth;
  }
  
  /* Ajustar z-index para móviles */
  .z-index-high,
  div[style*="z-index: 1000"] {
    z-index: 9999 !important;
  }
}

/* ========================================
   ORIENTACIÓN LANDSCAPE EN MÓVILES
   ======================================== */

@media (max-width: 768px) and (orientation: landscape) {
  /* Layout horizontal para landscape */
  .EditorContainer,
  div[style*="display: flex"][style*="flex: 1"][style*="background-color: var(--color-backgroundSecondary)"] {
    flex-direction: row !important;
  }
  
  .ScriptEditor,
  .CodeEditor {
    flex: 1 !important;
    height: 100vh !important;
  }
  
  .PreviewPanel {
    width: 40% !important;
    height: 100vh !important;
    border-top: none !important;
    border-left: 1px solid var(--color-border) !important;
  }
}

/* ========================================
   COMPATIBILIDAD CON NAVEGADORES MÓVILES
   ======================================== */

/* iOS Safari específico */
@supports (-webkit-touch-callout: none) {
  @media (max-width: 768px) {
    /* Ajustes específicos para iOS */
    body {
      -webkit-text-size-adjust: 100%;
    }
    
    input, textarea, select {
      -webkit-appearance: none;
      border-radius: 0;
    }
  }
}

/* Android Chrome específico */
@supports (display: -webkit-box) {
  @media (max-width: 768px) {
    /* Ajustes específicos para Android */
    .flex-container {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
    }
  }
}

/* ========================================
   ZOOM Y ESCALADO PARA ANDROID
   ======================================== */

/* Permitir zoom completo en Android */
@media (max-width: 768px) {
  /* Asegurar que el contenido se escale correctamente */
  html {
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    text-size-adjust: 100%;
  }
  
  body {
    /* Permitir zoom hacia afuera sin problemas */
    min-width: 320px;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
  
  #root {
    /* Asegurar que el root se escale correctamente */
    min-width: 320px;
    width: 100%;
    min-height: 100vh;
  }
  
  /* Mejorar el escalado del contenido principal */
  .AppContainer,
  div[style*="display: flex"][style*="flex-direction: column"][style*="height: 100vh"] {
    min-width: 320px;
    width: 100%;
    min-height: 100vh;
  }
  
  /* Asegurar que los paneles se adapten al zoom */
  .MainContent,
  .EditorContainer,
  .SidebarContainer {
    min-width: 0;
    flex-shrink: 1;
  }
  
  /* Mejorar la experiencia de zoom en elementos específicos */
  .ScriptEditor,
  .PreviewPanel,
  .TopBar {
    min-width: 280px; /* Mínimo para que sea usable con zoom */
  }
  
  /* Asegurar que los modales funcionen con zoom */
  .Modal,
  .ModalOverlay {
    min-width: 280px;
    width: 100%;
  }
  
  .ModalContent {
    min-width: 280px;
    max-width: 100%;
  }
}

/* ========================================
   OPTIMIZACIONES ESPECÍFICAS PARA ZOOM
   ======================================== */

/* Mejorar la experiencia cuando el usuario hace zoom */
@media (max-width: 768px) {
  /* Asegurar que los elementos interactivos sean accesibles */
  button,
  input,
  textarea,
  select,
  .Button,
  div[role="button"] {
    min-width: 44px;
    min-height: 44px;
    /* Mejorar la visibilidad con zoom */
    border: 1px solid transparent;
    transition: border-color 0.2s ease;
  }
  
  button:focus,
  input:focus,
  textarea:focus,
  select:focus {
    border-color: var(--color-primary);
    outline: 2px solid var(--color-primary);
    outline-offset: 2px;
  }
  
  /* Mejorar la legibilidad del texto con zoom */
  body {
    font-size: 16px; /* Tamaño base que funciona bien con zoom */
    line-height: 1.5;
  }
  
  /* Asegurar que las imágenes se escalen correctamente */
  img {
    max-width: 100%;
    height: auto;
  }
  
  /* Mejorar el scroll horizontal si es necesario */
  .overflow-x-auto,
  div[style*="overflow-x: auto"] {
    -webkit-overflow-scrolling: touch;
    scrollbar-width: thin;
  }
  
  /* Estilos para elementos que necesitan scroll horizontal */
  .horizontal-scroll {
    overflow-x: auto;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
  }
}

/* ========================================
   CONTROLES DE ZOOM PERSONALIZADOS
   ======================================== */

/* Clase para elementos que necesitan zoom personalizado */
.zoom-container {
  transform-origin: top left;
  transition: transform 0.3s ease;
}

/* Utilidades para diferentes niveles de zoom */
.zoom-75 { transform: scale(0.75); }
.zoom-90 { transform: scale(0.9); }
.zoom-100 { transform: scale(1); }
.zoom-110 { transform: scale(1.1); }
.zoom-125 { transform: scale(1.25); }

/* Mejorar la experiencia en pantallas muy pequeñas */
@media (max-width: 360px) {
  /* Ajustes adicionales para pantallas muy pequeñas */
  .MainContent,
  .EditorContainer {
    padding: 4px;
  }
  
  .ScriptEditor,
  .PreviewPanel {
    min-width: 260px;
  }
  
  /* Reducir aún más el padding en pantallas muy pequeñas */
  .PanelContainer,
  .Section {
    padding: 6px !important;
  }
  
  /* Ajustar tamaños de fuente para pantallas muy pequeñas */
  body {
    font-size: 14px;
  }
  
  .SectionTitle,
  h1, h2, h3, h4, h5, h6 {
    font-size: 14px !important;
  }
}
