/* Importar estilos responsivos */
@import './styles/responsive.css';

/* Variables CSS globales para breakpoints dinámicos */
:root {
  /* Breakpoints responsivos */
  --breakpoint-mobile: 480px;
  --breakpoint-tablet: 768px;
  --breakpoint-desktop: 1024px;
  --breakpoint-wide: 1200px;
  
  /* Layout responsivo */
  --sidebar-width-mobile: 100%;
  --sidebar-width-tablet: 100%;
  --sidebar-width-desktop: 60px;
  
  /* Espaciado responsivo */
  --spacing-xs: 4px;
  --spacing-sm: 8px;
  --spacing-md: 12px;
  --spacing-lg: 16px;
  --spacing-xl: 24px;
  
  /* Tipografía responsiva */
  --font-size-xs: 10px;
  --font-size-sm: 12px;
  --font-size-md: 14px;
  --font-size-lg: 16px;
  --font-size-xl: 18px;
  --font-size-2xl: 20px;
  
  /* Altura de toque para dispositivos móviles */
  --touch-target-size: 44px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-fontFamily, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-background);
  color: var(--color-textPrimary);
  overflow: hidden;
  transition: background-color 0.3s ease, color 0.3s ease;
  
  /* Optimizaciones para móviles */
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100vh;
  width: 100vw;
  
  /* Mejorar rendimiento en móviles */
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
}

/* Utilidades responsivas globales */
@media (max-width: 768px) {
  /* Reducir animaciones en móviles para mejor rendimiento */
  * {
    animation-duration: 0.2s !important;
    transition-duration: 0.2s !important;
  }
  
  /* Optimizar scroll */
  body {
    overflow-x: hidden;
  }
  
  /* Prevenir zoom en inputs */
  input, textarea, select {
    font-size: 16px !important;
  }
}
