/* Variables CSS para el sistema de temas */

/* Variables por defecto (Dark Default) - Mejorado para mejor contraste */
:root {
  /* Colores principales - Mejor contraste */
  --color-primary: #60a5fa;
  --color-primaryHover: #3b82f6;
  --color-secondary: #a1a1aa;
  
  /* Fondos - Mejor diferenciación con mayor contraste */
  --color-background: #0a0a0a;
  --color-backgroundSecondary: #151515;
  --color-backgroundTertiary: #202020;
  
  /* Superficies - Mejor contraste para modales y menús */
  --color-surface: #1e1e1e;
  --color-surfaceSecondary: #2a2a2a;
  --color-surfaceElevated: #353535;
  
  /* Bordes - Mayor contraste para mejor definición */
  --color-border: #4a4a4a;
  --color-borderSecondary: #5a5a5a;
  --color-borderFocus: #60a5fa;
  
  /* Texto - Mejor legibilidad */
  --color-textPrimary: #ffffff;
  --color-textSecondary: #e4e4e7;
  --color-textTertiary: #a1a1aa;
  --color-textInverse: #0f0f0f;
  
  /* Estados - Colores más vibrantes */
  --color-success: #22c55e;
  --color-warning: #fbbf24;
  --color-error: #f87171;
  --color-info: #60a5fa;
  
  /* Estados con hover - Mejor contraste */
  --color-successHover: #16a34a;
  --color-warningHover: #f59e0b;
  --color-errorHover: #ef4444;
  --color-infoHover: #3b82f6;
  
  /* Fondos de estados */
  --color-warningBackground: rgba(245, 158, 11, 0.1);
  --color-infoBackground: rgba(59, 130, 246, 0.1);
  --color-successBackground: rgba(16, 185, 129, 0.1);
  --color-errorBackground: rgba(239, 68, 68, 0.1);
  
  /* Sombras - Más profundidad */
  --color-shadow: rgba(0, 0, 0, 0.4);
  --color-shadowHover: rgba(0, 0, 0, 0.6);
  --color-shadowElevated: rgba(0, 0, 0, 0.8);
  --color-shadowStrong: rgba(0, 0, 0, 0.9);
  
  /* Gradientes - Mejor contraste */
  --color-gradientPrimary: linear-gradient(135deg, #60a5fa 0%, #3b82f6 100%);
  --color-gradientSecondary: linear-gradient(135deg, #1a1a1a 0%, #262626 100%);
  --color-gradientSurface: linear-gradient(135deg, #0f0f0f 0%, #1a1a1a 100%);
  
  /* Variables adicionales para mejor consistencia */
  --color-backdrop: rgba(0, 0, 0, 0.7);
  --color-primary-rgb: 96, 165, 250;
  
  /* Tipografía */
  --font-fontFamily: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  --font-fontSize-xs: 12px;
  --font-fontSize-sm: 14px;
  --font-fontSize-base: 16px;
  --font-fontSize-lg: 18px;
  --font-fontSize-xl: 20px;
  --font-fontSize-2xl: 24px;
  --font-fontSize-3xl: 30px;
  --font-fontWeight-normal: 400;
  --font-fontWeight-medium: 500;
  --font-fontWeight-semibold: 600;
  --font-fontWeight-bold: 700;
  --font-lineHeight-tight: 1.25;
  --font-lineHeight-normal: 1.5;
  --font-lineHeight-relaxed: 1.75;
  
  /* Espaciado */
  --spacing-xs: 4px;
  --spacing-sm: 8px;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;
  --spacing-2xl: 48px;
  
  /* Bordes redondeados */
  --radius-sm: 4px;
  --radius-md: 8px;
  --radius-lg: 12px;
  --radius-xl: 16px;
  --radius-full: 9999px;
}

/* Tema Light Default - Mejorado para mejor contraste */
.theme-light {
  /* Colores principales - Mejor contraste */
  --color-primary: #1d4ed8;
  --color-primaryHover: #1e40af;
  --color-secondary: #475569;
  
  /* Fondos - Mejor diferenciación */
  --color-background: #ffffff;
  --color-backgroundSecondary: #f1f5f9;
  --color-backgroundTertiary: #e2e8f0;
  
  /* Superficies - Mejor contraste */
  --color-surface: #ffffff;
  --color-surfaceSecondary: #f8fafc;
  --color-surfaceElevated: #ffffff;
  
  /* Bordes - Mayor contraste */
  --color-border: #cbd5e1;
  --color-borderSecondary: #94a3b8;
  --color-borderFocus: #1d4ed8;
  
  /* Texto - Mejor legibilidad */
  --color-textPrimary: #0f172a;
  --color-textSecondary: #475569;
  --color-textTertiary: #64748b;
  --color-textInverse: #ffffff;
  
  /* Estados - Colores más vibrantes */
  --color-success: #059669;
  --color-warning: #d97706;
  --color-error: #dc2626;
  --color-info: #2563eb;
  
  /* Estados con hover - Mejor contraste */
  --color-successHover: #047857;
  --color-warningHover: #b45309;
  --color-errorHover: #b91c1c;
  --color-infoHover: #1d4ed8;
  
  /* Fondos de estados */
  --color-warningBackground: rgba(245, 158, 11, 0.1);
  --color-infoBackground: rgba(59, 130, 246, 0.1);
  --color-successBackground: rgba(16, 185, 129, 0.1);
  --color-errorBackground: rgba(239, 68, 68, 0.1);
  
  /* Sombras - Más profundidad */
  --color-shadow: rgba(0, 0, 0, 0.15);
  --color-shadowHover: rgba(0, 0, 0, 0.25);
  --color-shadowElevated: rgba(0, 0, 0, 0.35);
  --color-shadowStrong: rgba(0, 0, 0, 0.45);
  
  /* Gradientes - Mejor contraste */
  --color-gradientPrimary: linear-gradient(135deg, #1d4ed8 0%, #2563eb 100%);
  --color-gradientSecondary: linear-gradient(135deg, #f1f5f9 0%, #e2e8f0 100%);
  --color-gradientSurface: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  
  /* Variables adicionales para mejor consistencia */
  --color-backdrop: rgba(0, 0, 0, 0.4);
  --color-primary-rgb: 29, 78, 216;
}

/* Estilos base para el tema light */
.theme-light body {
  background-color: var(--color-background);
  color: var(--color-textPrimary);
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Estilos para componentes específicos en tema light */
.theme-light .topbar-container {
  background-color: var(--color-surface);
  border-bottom: 1px solid var(--color-border);
}

.theme-light .sidebar-container {
  background-color: var(--color-surfaceSecondary);
  border-right: 1px solid var(--color-border);
}

.theme-light .panel-container {
  background-color: var(--color-surface);
  border: 1px solid var(--color-border);
}

.theme-light .button-primary {
  background-color: var(--color-primary);
  color: var(--color-textInverse);
  border: 1px solid var(--color-primary);
}

.theme-light .button-primary:hover {
  background-color: var(--color-primaryHover);
}

.theme-light .button-secondary {
  background-color: var(--color-surface);
  color: var(--color-textPrimary);
  border: 1px solid var(--color-border);
}

.theme-light .button-secondary:hover {
  background-color: var(--color-backgroundSecondary);
  border-color: var(--color-borderSecondary);
}

.theme-light .input-field {
  background-color: var(--color-surface);
  color: var(--color-textPrimary);
  border: 1px solid var(--color-border);
}

.theme-light .input-field:focus {
  border-color: var(--color-borderFocus);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}

.theme-light .card {
  background-color: var(--color-surface);
  border: 1px solid var(--color-border);
  box-shadow: 0 1px 3px var(--color-shadow);
}

.theme-light .card:hover {
  box-shadow: 0 4px 6px var(--color-shadowHover);
}

/* Transiciones suaves para cambios de tema */
* {
  transition: background-color 0.3s ease, 
              color 0.3s ease, 
              border-color 0.3s ease, 
              box-shadow 0.3s ease;
}
